body,
html {
  background-color: #fefefe;
  height: 100%;
  overflow-x: hidden;
  font-family: "Abel", sans-serif;
}

a,
a:hover {
  color: #333;
  text-decoration: none;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

p {
  padding: 0;
  margin: 0;
}

img {
  max-width: 100%;
}

input[type="text"] {
  border-radius: 10px;
  outline: none;
  padding: 5px 10px;
  width: 90%;
  max-width: 90%;
  border-style: none;
}

button {
  outline: none !important;
}

.container-fluid {
  padding: 0;
}

.dancing-script {
  font-family: "Dancing Script", cursive;
}

#loader-wrapper {
  display: -ms-flexbox;
  display: flex;
  height: 90vh;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.loader {
  border: 16px solid #f3f3f3; /* Light grey */
  border-top: 16px solid #333; /* Blue */
  border-radius: 50%;
  -webkit-animation: spin 2s linear infinite;
          animation: spin 2s linear infinite;
}

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

/* Header  */
#header {
  height: 70px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
  background-color: #fefefe;
  padding: 0 10px;
  -webkit-box-shadow: 0 4px 6px -6px #222;
          box-shadow: 0 4px 6px -6px #222;
}

#header-title {
  font-size: 2rem;
  /* color: #333; */
}

#header button {
  display: inline-block;
  border: none;
  text-decoration: none;
  text-align: center;
  -webkit-transition: background 250ms ease-in-out, -webkit-transform 150ms ease;
  transition: background 250ms ease-in-out, -webkit-transform 150ms ease;
  -o-transition: background 250ms ease-in-out, transform 150ms ease;
  transition: background 250ms ease-in-out, transform 150ms ease;
  transition: background 250ms ease-in-out, transform 150ms ease, -webkit-transform 150ms ease;
  -webkit-appearance: none;
  -moz-appearance: none;
  background-color: inherit;
}

/* Landing Page */
#landing-page {
  height: 90vh;
  width: 100vw;
  overflow: hidden;
  background-repeat: no-repeat;
  background-image: url(/img/prague-980732_1920.jpg);
  background-size: cover;
  background-position: center;
}

#landing-page-inner {
  display: -ms-flexbox;
  display: flex;
  height: 90vh;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

#landing-page-title {
  position: absolute;
  padding: 20px 10px;
  background-color: rgba(0, 0, 0, 0.5);
  color: #fefefe;
  border-radius: 10px;
}

#landing-page h1 {
  font-size: 2.2em;
}

#landing-page p {
  font-size: 1.2em;
}

#authors-menu {
  overflow-y: scroll;
  height: 100vh;
  padding: 0 5px;
  margin: 10px;
}

#list-authors li {
  margin: 10px;
}

/* Single Author  */

.list-container {
  max-width: 90vw;
  margin: 0 auto;
}

.list-numbers {
  font-size: 0.8em;
}

/* Drawer  */
li p {
  color: #000;
}

/* Flower  */
.black-rose {
  position: fixed;
  left: 0;
}

.flower {
  position: relative;
  bottom: 0;
  max-width: 80vw;
  margin: 0 auto;
}

/*
----------------------
----------------------
---------------------- */

/* Responsive Breakpoints  */

/* // Small devices (landscape phones, 576px and up) */
@media (min-width: 576px) {
  #landing-page-title {
    padding: 50px 10px;
  }

  #landing-page h1 {
    font-size: 2.5em;
  }

  #landing-page p {
    font-size: 1.5em;
  }
}

/* // Medium devices (tablets, 768px and up) */
@media (min-width: 768px) {
}

/* // Large devices (desktops, 992px and up) */
@media (min-width: 992px) {
  #landing-page h1 {
    font-size: 2.8em;
  }

  #landing-page p {
    font-size: 1.8em;
  }

  .list-container a,
  .list-container p,
  .list-container li,
  #list-authors p,
  #header p,
  button,
  input[type="text"] {
    font-size: 1.2em;
  }
}

/* // Extra large devices (large desktops, 1200px and up) */
@media (min-width: 1200px) {
}



